* {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  margin: 0;
  padding: 0;
}

.links {
  cursor: pointer;
  user-select: none;
}

.infText {
  color: white;
  transform: translateX(-100%);
  animation: slideIn 0.7s ease-out forwards;
}

#ContactInformationComponent {
  display: flex;
  justify-content: space-between;
  font-size: 1.5rem;
  background-color: rgb(188, 33, 59);
  width: 100%;
}

#ContactInformationComponentLeftBlock {
  display: flex;
  flex-direction: row;
  width: 50%;
  padding: 1rem;
}


#ContactInformationComponentRightBlock {
  display: flex;
  flex-direction: row;
  width: 50%;
  width: 4rem;
}

.about_me {
  padding: 1rem;
  margin-right: 2rem;
  transition: background-color 0.5s ease;
  color: white;
}

.about_me:hover {
  background-color: antiquewhite;
  padding: 1rem;
  margin-right: 2rem;
  color: black;
  transition: background-color 0.5s ease;
}

.notification {
  position: absolute;
  padding: 1rem;
  padding-left: 0.8rem;
  width: 15rem;
  height: 7rem;
  border-radius: 22px 22px;
  background-color: rgb(41, 145, 214);
}

.InformationAboutMe {
  position: absolute;
  z-index: 1000;
  background-color: rgb(41, 145, 214);
  padding: 1rem;
  width: 20rem;
}



@keyframes slideIn {
  from {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(0);
  }
}